body {
  width: 790px;
  padding: 0;
  margin: 0 auto;
  background-color: white;
}

.page {
  page-break-after: always;
  position: relative;
}

.page-details {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 10000;
}

.form-img {
  width: 100%;
}

.answer {
  position: absolute;
  bottom: 0;
}

.answer img {
  width: 100%;
  position: absolute;
}

.answer input {
  width: 100%;
  font-size: 12px;
  font-weight: bold;
  color: #141414;
  position: absolute;
  background: transparent;
}

.answer .check-mark {
  position: absolute;
}
.answer .check-mark::before, .answer .check-mark::after {
  content: "";
  height: 2px;
  width: 15px;
  background: black;
  position: absolute;
  transform: rotate(45deg);
}
.answer .check-mark::before {
  transform: rotate(-45deg);
}

